<template>
  <div class="hello">
    <!-- <h3>{{ msg }}</h3> -->
    
    <!-- 影院热映 -->
    <!-- 电影组件 -->
    <moviesitem :moviesitem="movies" :tit="tit"></moviesitem>
    <!-- 免费电影 -->
    <moviesitem :moviesitem="freeMovies" :tit="tit"></moviesitem>
    <!-- 新片推荐 -->
    <booksitem :moviesitem="newmovies"></booksitem>
    <!-- 分类预览 -->
    <h2 class="flyls">分类预览</h2>
    <div class="flook">
        <div class="flook-item" @click="openbooks('fiction')">小说</div>
        <div class="flook-item" @click="openbooks('love')">爱情</div>
        <div class="flook-item" @click="openbooks('history')">历史</div>
        <div class="flook-item" @click="openbooks('foreign')">外国文学</div>
        <div class="flook-item" @click="openbooks('youth')">青春</div>
        <div class="flook-item" @click="openbooks('inspiration')">励志</div>
        <div class="flook-item" @click="openbooks('essay')">随笔</div>
        <div class="flook-item" @click="openbooks('biography')">传记</div>
    </div>
    <div class="footerbox">
      <p>---------------------------- &nbsp;&nbsp;&nbsp;图书&nbsp;&nbsp;&nbsp; ----------------------------</p>
    </div>
  </div>
</template>
<script>
import qs from "qs"
import swiper from '../subcomponents/swiper.vue'
// 引入muijs
import mui from '../../lib/mui/js/mui.min.js'
//引入组件
//https://m.douban.com/rexxar/api/v2/subject_collection/filter_movie_comedy_hot/items?os=ios&for_mobile=1&callback=jsonp1&start=0&count=18&loc_id=108288&_=0
//https://m.douban.com/rexxar/api/v2/subject_collection/filter_movie_classic_hot/items?os=ios&for_mobile=1&callback=jsonp1&start=0&count=18&loc_id=108288&_=0
import moviesitem from '../subcomponents/moviesItem.vue'
import booksitem from '../subcomponents/booksitem.vue'
export default {
  name: 'homeCon',
  data () {
    return {
      msg: '首页',
      lunbolist:[{"adId":"1","storeName":"游日超市","storeId":"1","adTitle":"拉菲葡萄酒","img":"https://ykyg.qiqi0452.com/UpFile/AdvertisementImg/201811045213161.png","categoryId":"5","type":"2","goodsId":"5","goodsImage":"https://ykyg.qiqi0452.com/UpFile/ShopCommodity/201818045412011.jpg"},{"adId":"2","storeName":"游日超市","storeId":"1","adTitle":"三只松鼠芒果干","img":"https://ykyg.qiqi0452.com/UpFile/AdvertisementImg/201812040713151.png","categoryId":"7","type":"2","goodsId":"10","goodsImage":"https://ykyg.qiqi0452.com/UpFile/ShopCommodity/201819045112371.jpg"},{"adId":"3","storeName":"游日超市","storeId":"1","adTitle":"伯力爵黑巧克力味饼干","img":"https://ykyg.qiqi0452.com/UpFile/AdvertisementImg/201812040813131.png","categoryId":"8","type":"2","goodsId":"16","goodsImage":"https://ykyg.qiqi0452.com/UpFile/ShopCommodity/201819045512261.jpg"},{"adId":"4","storeName":"游日超市","storeId":"1","adTitle":"伊利安慕希原味酸奶","img":"https://ykyg.qiqi0452.com/UpFile/AdvertisementImg/201812040813421.png","categoryId":"9","type":"2","goodsId":"21","goodsImage":"https://ykyg.qiqi0452.com/UpFile/ShopCommodity/201819045812551.jpg"},{"adId":"5","storeName":"游日超市","storeId":"1","adTitle":"智利雪花李","img":"https://ykyg.qiqi0452.com/UpFile/AdvertisementImg/201812040913221.png","categoryId":"10","type":"2","goodsId":"26","goodsImage":"https://ykyg.qiqi0452.com/UpFile/ShopCommodity/201820040112311.jpg"}],
      url:this.HOST,
      uid:39,
      tit:'book',
      movies:{},
      freeMovies:{},
      newmovies:{},
      location:this.$store.getters.optLocation
    }
  },
  created(){
     this.getHotMovies();
     this.getFreeMovies();
     this.getNewMovies();
    // this.postlogin();
    //this.gettop()
  },
  methods:{
    //测试跨域，豆瓣api
    gettop(){
        this.$axios.get(this.url+"/v2/movie/top250").then((res)=>{
            console.log(res)
        }).catch((error)=>{
            console.log(error)
        })
    },
    //https://m.douban.com/rexxar/api/v2/最受关注的虚构类图书
    getHotMovies(){
        this.$axios.get(this.HOST+'subject_collection/book_fiction/items?os=ios&start=0&count=8&loc_id=0&_=0').then((res)=>{
            console.log(res.data)
            //https://m.douban.com/rexxar/api/v2/
            res.data.title=res.data.subject_collection.name;
            this.movies=res.data;
        }).catch((error)=>{
            console.log(error);
            if(error){
                console.log(error);
            }else{
                console.log("反馈出错，问题代码不符合")
            }
        })
    },
    //最受关注的非虚构类图书
    getNewMovies(){
        this.$axios.get(this.HOST+'subject_collection/market_product_book_mobile_web/items?os=ios&start=0&count=8&loc_id=0&_=0').then((res)=>{
            res.data.title=res.data.subject_collection.name;
            this.newmovies=res.data;
        }).catch((error)=>{
            console.log(error)
        })
    },
    //豆瓣书店
    getFreeMovies(){
      this.$axios.get(this.HOST+'subject_collection/book_nonfiction/items?os=ios&start=0&count=8&loc_id=0&_=0').then((res)=>{
          res.data.title=res.data.subject_collection.name.substr(0,12);
          this.freeMovies=res.data;
      }).catch((error)=>{
          console.log(error)
      })
    },
    openbooks(name){
        this.$router.push({ name: 'bookslist', params: { name: name }})
    },
    postlogin(){//一个模拟登录的接口
        this.$axios.post('https://www.wwtliu.com/sxtstu/blueberrypai/login.php',{
            user_id:'iwen@qq.com',
            password:'iwen123',
            verification_code:'crfvw'
        }).then((res)=>{
            console.log(res)
        }).catch((error)=>{
            console.log(error)
        })
    }
  },
  components:{
    "swiper-box":swiper,
    moviesitem,
    booksitem
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
@r:50rem;
*{touch-action:pan-y;}
.flyls{font-size: 15/@r;margin-top: 30/@r;padding-left: 10/@r;}
.mui-media img{background-color: #fff;}
body{background-color: #fff;}
.mui-grid-view.mui-grid-9{background-color: #fff;border:0;}
.mui-grid-view.mui-grid-9 .mui-table-view-cell{border:0;}
.mui-table-view-cell{
  background-color: #fff;
  img{width:50/@r;height:50/@r;}
}
.mui-grid-view.mui-grid-9 .mui-table-view-cell{padding:0 10/@r;}
.flook{
    padding:10/@r;
    display:flex;
    justify-content:space-between;
    flex-wrap:wrap;
    .flook-item{
        width:170/@r;
        font-size: 13/@r;
        color: #42bd56;
        margin-bottom: 10/@r;
        display:inline-block;
        line-height: 36/@r;
        height:36/@r;
        border:1px solid #e4e4e4;
        text-align: center;
        display:inline-block;
    }
}
h1, h3 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.mint-swipe{
    width:375/@r;height:150/@r;
    img{width:375/@r;height:150/@r;}
}
.footerbox{
  padding:10/@r;
  padding-top: 20/@r;
  p{text-align: center;color:#e4e4e4;font-size: 12/@r;}
}
</style>



