<template>
    <div class="grouplist">
        <div class="group-item" v-for="(item,index) in grouplist" :key="index">
            <h2>{{item.name}}</h2>
            <ul class="group">
                <li v-for="(it,i) in item.groups" :key="i">
                    <div class="group-meta">
                      <img class="group-icon" :src="it.avatar" alt="">
                      <span class="group-name">{{it.name}}</span>
                      <span class="group-member">{{it.topic_count}}人</span>
                    </div>  
                    <div class="group-topic">
                      <time></time>
                      <span class="topic-title">{{it.desc_abstract}}</span>
                    </div>  
                </li>
            </ul>
        </div>
    </div>
</template>
<script type="text/javascript">
     export default{
        name:'percen',
        data(){
            return{
                grouplist:{}
            }
        },
        created(){
            this.getgroup();
        },
        methods:{//https://m.douban.com/rexxar/api/v2/
            getgroup(){
                this.$axios.get(this.HOST+'group/rec_groups_for_newbies?ck=&for_mobile=1').then((res)=>{
                    console.log(res.data.rec_groups[0].classified_groups)
                    this.grouplist=res.data.rec_groups[0].classified_groups;
                })
            }
        }
     }
</script>
<style lang="less" scoped>
    @r:50rem;
ul.group li{list-style: none;border-bottom: 1px solid #e4e4e4;padding:10/@r 0;}
.grouplist{padding:8/@r 16/@r;}
.group-item{margin-bottom: 30/@r;}
.group-item h2{font-size: 16/@r;}
.group .group-meta {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
}
.group .group-meta .group-icon {
    border-radius: 3/@r;
    width: 40/@r;
    height: 40/@r;
    border: 1px solid #f1f1f1;
}
.group .group-meta .group-name {
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    -ms-flex: 1;
    flex: 1;
    padding-left: 10px;
    padding-right: 10px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.group .group-meta .group-member {
    color: #ccc;
    font-size: 15/@r;
}
.group .group-topic {
    margin-top: 4/@r;
    font-size: 14/@r;
    color: #aaa;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

</style>


