<template>
    <div class="movie_showing">
      <div class="top">
          <h2>{{moviesitem.title}}</h2>
          <a href="/movie/nowintheater?loc_id=108288">更多</a>
      </div>
      <div class="bot">
        <!-- <p>顶部滑动条quyu </p> -->
        <div class="mui-scroll-wrapper mui-slider-indicator mui-segmented-control mui-segmented-control-inverted">
            <div class="mui-scroll">
              <div class="ms-item mui-control-item mui-active" v-for="item in moviesitem.subject_collection_items" @click="routlinkbind(item.id)">
                <img :src="item.cover.url" alt="" />
                <p class="title">{{item.title}}</p>
                <p class="scope">{{item.rating?'评分：'+item.rating.value:'暂无评分'}}</p>
              </div>
            </div>
        </div>
      </div>
    </div>
</template>
<script type="text/javascript">
// 引入muijs
import mui from '../../lib/mui/js/mui.min.js'
     export default{
        name:'moviesitem',
        data(){
            return{
                
            }
        },
        mounted() {
            // 当 组件中的DOM结构被渲染好并放到页面中后，会执行这个 钩子函数
            // 如果要操作元素了，最好在 mounted 里面，因为，这里时候的 DOM 元素 是最新的
            // 2. 初始化滑动控件
            mui(".mui-scroll-wrapper").scroll({
                deceleration: 0.0005 //flick 减速系数，系数越大，滚动速度越慢，滚动距离越小，默认值0.0006
            });
        },
        methods:{
            routlinkbind(mId){
                if(this.tit=='book'){
                    this.$router.push({ name: 'booksinfo', params: { id: mId }})
                }else{
                    this.$router.push({ name: 'moviesinfo', params: { id: mId }})
                }
                
            },
        },
        props:["moviesitem",'tit']
     }
</script>
<style lang="less" scoped>
    @r:50rem;
*{touch-action:pan-y;}
body{background-color: #fff;}
.mui-segmented-control.mui-scroll-wrapper{
  height:189/@r;
}
.movie_showing{
  p{margin-bottom:0;}
  .top{
    display:flex;justify-content:space-between;
    line-height: 30/@r;
    padding: 20/@r 10/@r 0;
    h2{font-size: 15/@r;}
    a{font-size: 14/@r; }
  }
  .bot{
    
    .ms-item{
      text-align: center;
      flex:left;
      margin:0 10/@r;
      padding:0;
      img{
        width:100/@r;
        height:142/@r;
        vertical-align: middle;
      }
      .title{
        font-size: 14/@r;
        width:100/@r;
        height:26/@r;
        line-height: 26/@r;
        overflow:hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        text-align: center;
        color:#333;
      }
      .scope{
        font-size: 12/@r;
        height:22/@r;
        line-height: 22/@r;
        text-align: center;
      }
    } 
    
  }
}
</style>


