import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import homeCon from '@/components/tarbar/homeCon'
import shopCar from '@/components/tarbar/shopCar'
import findcon from '@/components/tarbar/findcon'
import percen from '@/components/tarbar/percen'
import test from '@/components/test/loadmore'
import classicslist from '@/components/classics/classicslist'
import moviesinfo from '@/components/classics/moviesinfo'
import discussions from '@/components/discussions/discussions'
import bookslist from '../components/books/bookslist'
import booksinfo from '../components/books/booksinfo'

Vue.use(Router)

export default new Router({
  routes: [
    {
        path: '/',
        redirect:'/home'
    },
    {
        path:'/home',
        name:'homeCon',
        component:homeCon
    },
    {
        path:'/shopcar',
        name:'shopCar',
        component:shopCar
    },
    {
        path:'/findcon',
        name:'findcon',
        component:findcon
    },
    {
        path:'/percen',
        name:'percen',
        component:percen
    },
    {
        path:'/home/classicslist/:name',
        name:'classicslist',
        component:classicslist
    },
    {
        path:'/home/colddoor/:name',
        component:classicslist
    },
    {
        path:'/home/mscore/:name',
        component:classicslist
    },
    {
        path:'/home/mhorror/:name',
        component:classicslist
    },
    {
        path:'/home/mchinese/:name',
        component:classicslist
    },
    {
        path:'/home/mscifi/:name',
        component:classicslist
    },
    {
        path:'/home/moviesinfo/:id',
        name:'moviesinfo',
        component:moviesinfo
    },
    {
        path:'/discussions/:id/:str',
        name:'discussions',
        component:discussions
    },
    {
        path:'/shopcar/bookslist/:name',
        name:'bookslist',
        component:bookslist
    },
    {
        path:'/shopcar/booksinfo/:id',
        name:'booksinfo',
        component:booksinfo
    },
    {//test测试
        path:'/test',
        name:'test',
        component:test
    }
  ],
  linkActiveClass:'mui-active'//覆盖默认高亮的类 router-lick-active
})



