<template>
    <div class="minfo">
        <!-- t影片主要信息 -->
        <div class="maininfo">
            <h1>{{moviesInfo.title}}</h1>
            <div class="def">
                <div class="left">
                    <p class="rating-stars"><span>{{moviesInfo.extra?'评分:'+moviesInfo.extra.rating_group.rating.value:'暂无评分'}}</span><span>{{moviesInfo.extra?moviesInfo.extra.rating_group.rating.count+'人评价':'暂无人评价'}}</span></p>
                    <p class="meta">{{moviesInfo.extra?moviesInfo.extra.short_info:''}}</p>
                </div>
                <div class="right">
                    <img :src="moviesInfo.cover_img?moviesInfo.cover_img.url:'../../assets/p1.jpg'" alt="" />
                </div> 
            </div>
            <div class="btns">
                <mt-button size="small" type="danger" plain>想看</mt-button> 
                <mt-button size="small" type="danger" plain>看过</mt-button>
            </div>
            <!-- 头号玩家的剧情简介 -->
            <div class="subject-intro">
                <h2>{{moviesInfo.title}}的内容简介</h2>
                <transition name="slide">
                <div class="bd" v-show="showInfo" data-content="" v-html="moviesInfo.desc"></div>
                </transition>
                <div class="bd" v-if="!showInfo" data-content="">{{moviesInfo.descsmall}}<a v-show="hideis" class="expand" href="javascript:;" @click.prevent="bindexpand" >(展开)</a> </div>
            </div>
            <!-- 头号玩家的短评(196485) -->
            <div class="subject-intro">
                <h2>{{moviesInfo.title}}的短评({{sortcomdp.total}})</h2>
                <div class="bd" data-content="">
                    <ul data-reactroot="" class="list comment-list">
                        <li class="" v-for="item in sortcomdp.interests" :key="item.id">
                            <div class="desc"><a href="javascript:;"><img :src="item.user.avatar?item.user.avatar:'https://img1.doubanio.com/icon/up1005928-127.jpg'" alt="影志"></a>
                                <div class="user-info">
                                    <div>
                                        <span>影志</span>
                                        <span class="rating-stars" data-rating="5">
                                            {{item.rating?'评分:'+item.rating.value:'暂无评分'}}
                                        </span>
                                    </div>                                    
                                    <div class="date">{{item.gtime}}前</div>
                                </div>
                            </div>
                            <p class="miaoshu">{{item.comment}}</p>
                            <div class="btn-info">
                                <div class="ic-btn ic-btn-like  left ">
                                    <span class="text">{{item.vote_count}}</span>
                                </div>                                
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
            <!-- 评论 -->
            <div class="comment">
                <h2 class="tii">讨论({{commentlist.total}})</h2>
                <ul class="list comment_list">
                    <li v-for="item in commentlist.forum_topics" :key="item.id">
                        <p class="pltit">{{item.title}}</p>
                        <p class="huiying">回应({{item.comments_count}})</p>
                    </li>
                </ul>
                <p class="more_comment" @click="openComment(id)">查看更多评论</p>
            </div>

        </div>
    </div>
</template>
<script type="text/javascript">
     export default{
        name:'moviesinfo',
        data(){
            return{
                id:this.$route.params.id,
                moviesInfo:{},
                showInfo:false,
                hideis:true,
                sortcomdp:{},
                commentlist:{}
            }
        },
        created(){
            this.getMoviesInfo();
            this.getsortcom();
            this.getcomment();
        },
        methods:{
            /*bindhandleClick(){
                window.location.href="https://accounts.douban.com/login?source=mobile&redir=https%3A%2F%2Fm.douban.com%2Fmovie%2Fsubject%2F4920389%2F";
            }*/
            getMoviesInfo(){
                this.$axios.get(this.HOST+'elessar/subject/'+this.id).then((res)=>{
                    res.data.descsmall=res.data.desc.replace(/<\/?[^>]*>/g,'').substring(0,100);
                    if(res.data.desc.replace(/<\/?[^>]*>/g,'').length<=100){
                        this.hideis=false;
                    }                    
                    this.moviesInfo=res.data;
                })
            },
            bindexpand(){
                this.showInfo=!this.showInfo;
            },
            getsortcom(){
                this.$axios.get(this.HOST+'movie/'+this.id+'/interests?count=4&order_by=hot&start=0&ck=&for_mobile=1').then((res)=>{
     
                    res.data.interests.forEach((item)=>{
                        if(new Date().getFullYear()-item.create_time.substring(0,4)!=0){
                            item.gtime=new Date().getFullYear()-item.create_time.substring(0,4)+'年';
                        }
                        if(new Date().getFullYear()-item.create_time.substring(0,4)==0){
                            item.gtime=(new Date().getMonth()+1)-item.create_time.substring(5,7)+'月';
                        }
                        if((new Date().getMonth()+1)-item.create_time.substring(5,7)==0){
                            item.gtime=new Date().getDate()-item.create_time.substring(8,10)+'天';
                            console.log(new Date().getDate()-item.create_time.substring(8,10))
                        }

                    })
                    this.sortcomdp=res.data;
                })
            },
            getcomment(){
                this.$axios.get(this.HOST+'movie/'+this.id+'/forum_topics?start=0&count=5&ck=&for_mobile=1').then((res)=>{
                    this.commentlist=res.data;
                })
            },
            openComment(mId){
                //https://m.douban.com/rexxar/api/v2/movie/1292052/forum_topics?start=0&count=10&episode=0&ck=&for_mobile=1
                //https://m.douban.com/rexxar/api/v2/movie/1292052/interests?count=20&order_by=hot&start=0&ck=&for_mobile=1
                this.$router.push({ name: 'discussions', params: { id: mId ,str:'forum_topics',name:'movie'}})
            }
        }
     }
</script>
<style lang="less" scoped>
    @r:50rem;
    /* 可以设置不同的进入和离开动画 */
/* 设置持续时间和动画函数 */
.slide-leave-active,.slide-enter-active {
  transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-enter, .slide-leave-to{
  transform: translateX(8px);
}
.tii{color: #aaa;margin: 0 0 10/@r; font-size: 15/@r;}
.more_comment{text-align: center;padding:4/@r;color:#0ae;}
.comment_list{   
    li{
        border-bottom: 1px solid #e4e4e4;
        padding:6/@r 4/@r;
        .huiying{color:#0ae;}
    }
    p{margin-bottom: 0;line-height: 26/@r;}
}
    .ic-btn-like::before {background-image: url("https://img3.doubanio.com/f/talion/7a0756b3b6e67b59ea88653bc0cfa14f61ff219d/pics/card/ic_like_gray.svg");margin-left: 40/@r;}
    .ic-btn-like .text{ font-size:12/@r;color: #ccc;
    margin-left: 3/@r;
    position: relative;
    left: 0;
    top: -3/@r;}
    .ic-btn::before {
    content: '';
    width: 20px;
    height: 20px;
    background-position: center center;
    background-repeat: no-repeat;
    display: inline-block;
    margin-bottom: -2px;
}
    .maininfo{
        .subject-intro{
            margin-bottom: 30/@r;
            h2{
                color: #aaa;
                margin: 0 0 15/@r;
                font-size: 15/@r;
            }
            .bd{
                font-size: 15/@r;
                line-height: 22/@r;
                word-wrap: break-word;
            }
            .comment-list{
                li{
                    border-bottom: 1px solid #e4e4e4;
                    p.miaoshu{padding: 0 0 0 40/@r;line-height: 22/@r;color: #494949;}
                    .user-info{
                        float:left;
                        display:flex;
                        height:36/@r;
                        line-height: 36/@r;
                        justify-content:space-between;
                        width:6.14rem;
                        strong{
                            font-size: 15/@r;
                            display: inline-block;
                            vertical-align: text-top;
                        }
                        .date{color:#999;font-size: 12/@r;}
                        .rating-stars{
                            font-size: 12/@r;
                            margin-left: 6/@r;
                            vertical-align:middle;
                        }
                        .date{
                            font-size: 12/@r;
                        }
                    }
                    .desc{
                        img{
                                width: 36/@r;
                                height:36/@r;
                                border-radius: 50%;
                                vertical-align: text-top;
                                margin-right: 10/@r;
                                float: left;
                        }
                    }
                }
            }
        }
        padding:10/@r;
        h1{
            margin: 10/@r 0 5/@r;
            font-size: 18/@r;
            line-height:26/@r;
            word-break: break-all;
        }
        .btns{
            display:flex;
            margin:30/@r 0;
            justify-content:space-between;
            .mint-button{
                width:45%;
            }
        }
        .def{
            display:flex;
            justify-content:space-between;
            .left{
                width:230/@r;
                height:150/@r;
                .rating-stars{
                    font-size: 12/@r;
                    span{
                        font-size: 12/@r;
                    }
                    span:first-child{
                        color:#666;
                    }
                    span:last-child{color:#999;margin-left: 20/@r;}
                }
            }
            .right{
                width:100/@r;
                height:150/@r;
                img{
                    width:100/@r;
                    height:150/@r;
                }
            }
        }
    }
</style>


