<template>
    <div class="box">
        <h3>{{bigtitle}}</h3>
        <div class="page-loadmore-wrapper movieslist" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
            <mt-loadmore :bottom-method="loadBottom" @translate-change="translateChange" @bottom-status-change="handleBottomChange" :bottom-all-loaded="allLoaded" ref="loadmore">
                <ul class="page-loadmore-list">
                  <li v-for="item in list" class="page-loadmore-listitem" @click="routlinkbind(item.id)">
                        <img v-lazy="item.cover.url" alt="" />
                      <!--<img src="../../assets/p1.jpg" alt="" /> -->
                      <h4>{{item.title}}</h4>
                      <p>{{item.rating?'评分:'+item.rating.value:'暂无评分'}}</p>
                  </li>
                </ul>  
                <div slot="bottom" class="mint-loadmore-bottom">
                  <span v-show="bottomStatus !== 'loading'" :class="{ 'is-rotate': bottomStatus === 'drop' }">↑</span>
                  <span v-show="bottomStatus === 'loading'">
                    <mt-spinner type="snake"></mt-spinner>
                  </span>
                </div>
            </mt-loadmore>
        </div>
    </div>
</template>
<script type="text/javascript">
export default{
    name:'bookslist',
    data(){
        return{
            topStatus: '',
            list:[],
            bigtitle:'',
            locname:this.$route.params.name,
            allLoaded: false,
            bottomStatus: '',
            wrapperHeight: 560,
            translate: 0,
            moveTranslate: 0,
            page:0
        }
    },
    created(){
        this.getcMovies(0);
        this.zhlocanme();
    },
    methods:{
        zhlocanme(){
            //classic
            switch(this.locname)
            {
            case 'fiction':
              this.bigtitle="小说"
              break;
            case 'love':
              this.bigtitle="爱情"
              break;
            case 'history':
              this.bigtitle="历史"
              break;
            case 'foreign':
              this.bigtitle="外国文学"
              break;
            case 'youth':
              this.bigtitle="青春"
              break;
            case 'inspiration':
              this.bigtitle="励志"
              break;
            case 'essay':
              this.bigtitle="随笔"
              break;
            default:
              this.bigtitle="传记"
            }
        },
        getcMovies(page){
            //https://m.douban.com/rexxar/api/v2/subject_collection/filter_book_fiction_hot/items?os=ios&for_mobile=1&callback=jsonp1&start=0&count=18&loc_id=0&_=0
            //https://m.douban.com/rexxar/api/v2/subject_collection/filter_book_love_hot/items?os=ios&for_mobile=1&callback=jsonp1&start=0&count=18&loc_id=0&_=0
            this.$axios.get(this.HOST+'subject_collection/filter_book_'+this.locname+'_hot/items?os=ios&for_mobile=1&start='+(this.page*18)+'&count=18&loc_id=108288&_=0').then((res)=>{
                /*res.data.subject_collection_items.forEach((item)=>{
                    // + Math.random() 
                    //item.cover.url=item.cover.url+'?'+ Math.random() ;
                })*/
                this.list=this.list.concat(res.data.subject_collection_items);
                console.log(this.list)
            })
        },
        handleBottomChange(status) {
            this.bottomStatus = status;
        },
        
        loadBottom() {
            console.log(this.page)
            setTimeout(() => { 
                this.page++;                
              if (this.list.length >= 18*(this.page)) {
                 this.getcMovies(this.page);    
              } else {
                this.allLoaded = true;
              }
              this.$refs.loadmore.onBottomLoaded();              
              
            }, 1600);
        },
        translateChange(translate) {
            const translateNum = +translate;
            this.translate = translateNum.toFixed(2);
            this.moveTranslate = (1 + translateNum / 70).toFixed(2);
        },
        routlinkbind(mId){
            this.$router.push({ name: 'booksinfo', params: { id: mId }})
        },

    },
    mounted() {
      this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;
      if(this.wrapperHeight<500){
        this.wrapperHeight=502;
      }
    }
}
</script>
<style lang="less" scoped>
@r:50rem;
li{list-style: none;}
.box{
    min-height: 560/@r;
    h3{
        text-align: center;
        padding:6/@r 0;
        font-size: 16/@r;
    }
}
.loading-background, .mint-loadmore-top span {
    -webkit-transition: .2s linear;
    transition: .2s linear;
}
.mint-loadmore-top span {
    display: inline-block;
    vertical-align: middle;
}

.mint-loadmore-top span.is-rotate {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg);
}

.page-loadmore .mint-spinner {
    display: inline-block;
    vertical-align: middle;
}

.page-loadmore-desc {
    text-align: center;
    color: #666;
    padding-bottom: 5px;
}

.page-loadmore-desc:last-of-type,
.page-loadmore-listitem {
    img{width:100/@r;height:140/@r;}
    h4{font-size: 14/@r;text-align: center;overflow:hidden;width:100/@r;text-overflow: ellipsis;white-space: nowrap;}
    p{font-size: 12/@r;width:100/@r;text-align: center;}

}
.page-loadmore-list{ padding:0 10/@r;
    display:flex;
    justify-content:space-between;
    flex-wrap: wrap;
}

.page-loadmore-listitem:first-child {

}

.page-loadmore-wrapper {
    overflow: scroll;
}
.mint-loadmore-bottom span {
    display: inline-block;
    -webkit-transition: .2s linear;
    transition: .2s linear;
    vertical-align: middle;
}

.mint-loadmore-bottom span.is-rotate {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg);
}
</style>


