<template>
  <div id="app">
  <!-- tou头部 -->
    <mt-header title="易看易购" fixed >
      <router-link to="/" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
    </mt-header>
    <!-- 内容区 -->
    <!-- 中间路由存放 -->
    <transition>
      <router-view/>
    </transition>
    <!-- 底部 -->
    <nav class="mui-bar mui-bar-tab">
      <router-link class="mui-tab-item-ex " to="/home">
        <span class="mui-icon mui-icon-home"></span>
        <span class="mui-tab-label">首页</span>
      </router-link>
      <router-link class="mui-tab-item-ex" to="/shopcar">
        <span class="mui-icon mui-icon-extra mui-icon-extra-xiaoshuo"><!-- <span class="mui-badge">9</span> --></span>
        <span class="mui-tab-label">图书</span>
      </router-link>
      <router-link class="mui-tab-item-ex" to="/findcon">
        <span class="mui-icon mui-icon-videocam"></span>
        <span class="mui-tab-label">广播</span>
      </router-link>
      <router-link class="mui-tab-item-ex" to="/percen">
        <span class="mui-icon mui-icon-extra mui-icon-extra-peoples"></span>
        <span class="mui-tab-label">小组</span>
      </router-link>
    </nav>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style lang="less">
@r:50rem;
body{background-color: #fff;}
*{margin:0;padding:0;font-size: 16/@r;}
h3{font-size: 16/@r;}
.mint-header.is-fixed{z-index: 99;}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  padding-top: 40/@r;
  padding-bottom: 50/@r;
  overflow-x: hidden;
}
img{background-color: #eee;}
.v-enter{
  opacity: 0;
  transform: translateX(100%);  
}
.v-leave-to{
  opacity: 0;
  transform: translateX(-100%);
  position:absolute;
}
.v-enter-active,
.v-leave-active{
  transition: all .5s ease;
}
.mui-bar-tab .mui-tab-item-ex.mui-active {
    color: #007aff;
}
.mui-bar-tab .mui-tab-item-ex {
    display: table-cell;
    overflow: hidden;
    width: 1%;
    height: 50px;
    text-align: center;
    vertical-align: middle;
    white-space: nowrap;
    text-overflow: ellipsis;
    color: #929292;
}
.mui-bar-tab .mui-tab-item-ex .mui-icon {
    top: 3px;
    width: 24px;
    height: 24px;
    padding-top: 0;
    padding-bottom: 0;
}
.mui-bar-tab .mui-tab-item-ex .mui-icon~.mui-tab-label {
    font-size: 11px;
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
}
</style>



