<template>
    <div class="discussion">
        <div class="title">
            <span class="mtitle">{{moviesInfo.title}}</span><span >{{moviesInfo.extra?moviesInfo.extra.rating_group.rating.value:'暂无评'}}分</span>
        </div>
        <div class="wtitle">
            <div >讨论({{total}})</div>
            <div class="addhuat"> + 添加话题</div>
        </div>
        <ul class="comlist">
            <li v-for="item in commentlists" :key="item.id">
                <div class="nei">{{item.title}}</div>
                <div class="huiying">{{item.comments_count}}回应</div>
            </li>
        </ul>
        <p class="morecom" @click="bindmore">{{moretip}}</p>
    </div>
</template>
<script type="text/javascript">
     export default{
        name:'discussions',
        data(){
            return{
                id:this.$route.params.id,
                cname:this.$route.params.str,
                tit:this.$route.params.name,
                commentlists:[],
                total:'',
                moretip:'更多',
                moviesInfo:{},
                page:0
            }
        },
        created(){
            this.getMoviesInfo();
            this.getcomlists();
        },
        methods:{
            getMoviesInfo(){
                this.$axios.get(this.HOST+'elessar/subject/'+this.id).then((res)=>{                   
                    this.moviesInfo=res.data;
                })
            },
            getcomlists(){
                this.moretip="加载中...";
                this.$axios.get(this.HOST+this.tit+'/'+this.id+'/forum_topics?start='+this.page*10+'&count=10&episode=0&ck=&for_mobile=1').then((res)=>{
                    console.log(res.data)
                    if(res.data.count==10){
                        this.total=res.data.total;
                        this.moretip="更多";
                        this.commentlists=this.commentlists.concat(res.data.forum_topics);
                    }                    
                })
                //https://m.douban.com/rexxar/api/v2/movie/1292052/forum_topics?start=0&count=10&episode=0&ck=&for_mobile=1
                
            },
            bindmore(){
                this.page++;
                if( this.commentlists.length>=this.page*10){
                    this.getcomlists();
                }else{
                    this.moretip="我是有底线的";
                }             
            }
        }
     }
</script>
<style lang="less" scoped>
    @r:50rem;
    .discussion{padding:10/@r;}
    .title{
        border-bottom: 1px solid #e4e4e4;
        padding:6/@r 0;
        .mtitle{
            color: #42bd56;
            font-size: 15/@r;
            margin-right: 10/@r;
        }
        span{color:#999;font-size: 14/@r;}
    }
    .morecom{color: #42bd56;font-size: 14/@r;text-align: center;padding:10/@r 0 10/@r;}
    .wtitle{
        display:flex;
        padding:14/@r 0 8/@r;
        justify-content:space-between;
        align-items:center;
        border-bottom: 1px solid #e4e4e4;
        div{
            font-size: 15/@r;
            color:#999;
        }
        .addhuat{
            color: #42bd56;
            font-size: 14/@r;
        }
    }
    .comlist{
        li{
            padding:8/@r 0;
            border-bottom: 1px solid #e4e4e4;
            .nei{
                color:#333;
                font-size: 14/@r;
                padding-bottom: 2/@r;
            }
            .huiying{
                color: #42bd56;
                font-size: 13/@r;
            }
        }
    }
</style>


