<template>
    <div>
       <!--  <h3>发现</h3> -->
        <div class="mui-card" v-for="item in broadcastlist" :key="item.status.id">
            <!--页眉，放置标题-->
            <div class="mui-card-header mui-card-media">
                <img src="../../assets/logo.png" />
                <div class="mui-media-body">
                    <span>{{item.status.reshared_status?item.status.reshared_status.author.name:item.status.author.name}}</span><span>{{item.status.reshared_status?item.status.reshared_status.activity:item.status.activity}}</span>
                    <p>2016-06-30 15:30</p>
                </div>
            </div>
            <!--内容区-->
            <div class="mui-card-content">
                <div class="tit">{{item.status.card?item.status.card.title:''}}</div>
                <div class="def">{{item.status.card?item.status.card.subtitle:''}}</div>

            </div>
            <!--页脚，放置补充信息或支持的操作-->
            <!-- <div class="mui-card-footer">页脚</div> -->
        </div>
    </div>
</template>
<script type="text/javascript">
     export default{
        name:'findcon',
        data(){
            return{
                broadcastlist:[]
            }
        },
        created(){
            this.getbroadcast();
        },
        methods:{
            getbroadcast(){//https://m.douban.com/rexxar/api/v2/
                this.$axios.get(this.HOST+'status/anonymous_timeline?max_id=&ck=&for_mobile=1').then((res)=>{
                    console.log(res);
                    this.broadcastlist=res.data.items;
                })
            }            
        }
     }
</script>
<style lang="less" scoped>
    @r:50rem;
    .mui-card{margin-bottom: 20/@r;}
    .mui-card-header{
        .mui-media-body{
            font-size: 15/@r;
            p{
                font-size: 12/@r;
            }
        }
    }
    .mui-card-content{
        font-size: 13/@r;
        padding:10/@r;
        padding-left:50/@r;
        .tit{
            font-size: 14/@r;
            margin-bottom: 6/@r;
            line-height: 24/@r;
        }
        .def{
            font-size: 13/@r;
            line-height: 24/@r;
        }
    }
</style>


